.home {
    display: flex;
    flex-direction: column;
    height: 100vh;
}
.home-header {
    display: flex;
    justify-content: space-between;
    padding: 20px 30px;
    align-items: center;
}

.home-header a {
    margin-right: 20px;
    text-decoration: inherit;
    color: rgba(0, 0, 0, 0.87);
    font-size: 15px;
}

.home-header a:hover {
    text-decoration:underline;
}

.header-right { 
    display: flex;
    align-items: center;
    min-width: 13vw;
    justify-content: space-between;
}

.header-right > .MuiSvgIcon-root {
    margin-right: 20px;
}

.home-body { 
    flex: 1;
    display: flex;
    margin-top: 8%;
    flex-direction: column;
}

.home-body > img {
    object-fit: contain;
    height: 100px;
}


@media only screen and (max-width: 600px) {
    .header-left {
        display: none;
    }
    .header-right {
        display: flex;
        margin-left: 9rem;
    }
    .home-body > img {
        margin-top: 6rem;
    }
  }
